import { createApp } from 'vue'
import App from './App.vue'
import router from './router'

import axios from 'axios'

import 'lib-flexible/flexible'

import { 
  Tabbar,
  TabbarItem,
  Search,
  Sidebar,
  SidebarItem,
  Toast,
  Tag,
  NavBar,
  Stepper,
  ActionBar, 
  ActionBarIcon,
  ActionBarButton,
  Form,
  Field,
  Button,
  Popup,
  Checkbox,
  SwipeCell,
  SubmitBar,
  Empty,
  Icon,
  AddressList,
  AddressEdit,
  Tab,
  Tabs,
  Dialog,
  Cell,
  Uploader,
  Swipe,
  SwipeItem
 } from 'vant';

 import { tokenUrl } from './token/tokenUrl'

//设置请求基础路径
axios.defaults.baseURL = 'http://www.kangliuyong.com:10002';

//添加一个axios请求之前拦截器
axios.interceptors.request.use((config): any => {
  //config: 请求配置(请求头, 请求参数, 请求类型, 请求路径, ....)

  let tokenString: string | null = localStorage.getItem('token');

  let appkey: string = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';

  //处理请求的公共参数
  if (config.method === 'get') {
    config.params = config.params || {};
    config.params.appkey = appkey;

    if (tokenUrl.indexOf((<any>config).url) > -1) {
      config.params.tokenString = tokenString;
    }

  } else if (config.method === 'post') {
    config.data = config.data || {};

    if (tokenUrl.indexOf((<any>config).url) > -1) {
      config.data.tokenString = tokenString;
    }

    //序列化post请求参数
    let dataParams: string = '';
    for (let key in config.data) {
      dataParams += `${key}=${config.data[key]}&`
    }
    dataParams += `appkey=${appkey}`;

    config.data = dataParams;

  }

  //启动加载提示
  Toast.loading({
    message: '加载中...',
    //禁止穿透点击
    forbidClick: true,
    loadingType: 'spinner',
    //不会自动关闭提示
    duration: 0
  })

  return config;
}, (err): any => {
  
  //关闭加载提示
  Toast.clear();
  return Promise.reject(err);
})

//添加一个响应之前拦截器
axios.interceptors.response.use((res): object => {
  //res: 服务器响应数据
  // 

  //关闭加载提示
  Toast.clear();

  return res;
}, (err): any => {
  

  //关闭加载提示
  Toast.clear();

  return Promise.reject(err);
})

createApp(App)
  .use(router)
  .use(Tabbar)
  .use(TabbarItem)
  .use(Search)
  .use(Sidebar)
  .use(SidebarItem)
  .use(Toast)
  .use(Tag)
  .use(NavBar)
  .use(Stepper)
  .use(ActionBar)
  .use(ActionBarIcon)
  .use(ActionBarButton)
  .use(Form)
  .use(Field)
  .use(Button)
  .use(Popup)
  .use(Checkbox)
  .use(SwipeCell)
  .use(SubmitBar)
  .use(Empty)
  .use(Icon)
  .use(AddressList)
  .use(AddressEdit)
  .use(Tabs)
  .use(Tab)
  .use(Dialog)
  .use(Cell)
  .use(Uploader)
  .use(Swipe)
  .use(SwipeItem)
  .use(Search)
  .mount('#app')
